import React from "react";
import {
     Card,
     CardImg,
     CardBody,
     CardTitle,
     CardText,
     Button,
} from "reactstrap";
import Zoom from "react-reveal/Zoom";
import Fade from "react-reveal/Fade";

function RenderProjectDetails({ project }) {
     if (project !== null) {
          return (
               <div className="project-card">
                    <Fade bottom>
                         <Card className="card-details">
                              <CardImg
                                   top
                                   width="100%"
                                   height="50%"
                                   src={project.image}
                                   alt={project.title}
                                   className="card-img"
                              />
                              <CardBody>
                                   <CardTitle className="project-title">
                                        {" "}
                                        {project.title}{" "}
                                   </CardTitle>
                                   <CardText className="project-description">
                                        {" "}
                                        {project.description}{" "}
                                   </CardText>
                                   <span className="m-btn">
                                        {" "}
                                        <Button
                                             className="Resume-btn"
                                             href={project.github}
                                             rel="noopner noreferrer"
                                             target="_blank"
                                        >
                                             <span className="fa fa-github"></span>{" "}
                                             Github{" "}
                                        </Button>{" "}
                                   </span>
                                   {project.demo ? (
                                        <Button
                                             className="Resume-btn"
                                             href={project.demo}
                                             target="_blank"
                                        >
                                             <span className="fa fa-external-link"></span>{" "}
                                             Live Demo{" "}
                                        </Button>
                                   ) : null}
                              </CardBody>
                         </Card>
                    </Fade>
               </div>
          );
     } else {
          return <div> </div>;
     }
}

function Projects(props) {
     const projectDetails = props.project.map((proj) => {
          return (
               <div key={proj.id} className="col-12 col-sm-12 col-md-4">
                    <RenderProjectDetails project={proj} />
               </div>
          );
     });

     return (
          <div id="Projects" className="container">
               <div className="project-start proj-head">
                    <Zoom top cascade>
                         <h1 className="section-start">
                              <span className="pre-txt">04.</span> Projects{" "}
                         </h1>
                    </Zoom>
                    <div className="row">{projectDetails}</div>
               </div>
          </div>
     );
}

export default Projects;
