import React, { Component } from "react";
import {
     Nav,
     NavbarBrand,
     Navbar,
     NavItem,
     NavLink,
     NavbarToggler,
     Collapse,
} from "reactstrap";
import Hi from "../images/hi.png";
import Banner from "../images/banner-1.png";
import Typical from "react-typical";
import Fade from "react-reveal/Fade";

class Header extends Component {
     constructor(props) {
          super(props);

          this.state = {
               isNavOpen: false,
          };

          this.toggleNav = this.toggleNav.bind(this);
     }

     toggleNav() {
          this.setState({
               isNavOpen: !this.state.isNavOpen,
          });
     }

     render() {
          return (
               <React.Fragment>
                    <Navbar className="navbar-dark fixed-top" expand="md">
                         <div className="container">
                              <NavbarToggler onClick={this.toggleNav} />
                              <Fade left>
                                   <NavbarBrand
                                        className="navBrand navbar-brand"
                                        href="/#top"
                                   >
                                        <h1 className="name">
                                             {" "}
                                             Md Yaseen Ahmed
                                        </h1>
                                   </NavbarBrand>
                              </Fade>

                              <Collapse isOpen={this.state.isNavOpen} navbar>
                                   <Nav navbar></Nav>

                                   <Nav className="ml-auto" navbar>
                                        <NavItem>
                                             <NavLink
                                                  className="nav-link"
                                                  onClick={this.toggleNav}
                                                  href="/#About-Me"
                                             >
                                                  About
                                             </NavLink>
                                        </NavItem>

                                        <NavItem>
                                             <NavLink
                                                  className="nav-link"
                                                  onClick={this.toggleNav}
                                                  href="/#Education"
                                             >
                                                  Education
                                             </NavLink>
                                        </NavItem>

                                        <NavItem>
                                             <NavLink
                                                  className="nav-link"
                                                  onClick={this.toggleNav}
                                                  href="/#Skills"
                                             >
                                                  Skills
                                             </NavLink>
                                        </NavItem>

                                        <NavItem>
                                             <NavLink
                                                  className="nav-link"
                                                  onClick={this.toggleNav}
                                                  href="/#Projects"
                                             >
                                                  Projects
                                             </NavLink>
                                        </NavItem>

                                        {/*<NavItem>
					           		<NavLink className="nav-link" href="/#Certifications">
					            		My Cert.
					           		</NavLink>
					           	</NavItem>*/}

                                        <NavItem>
                                             <NavLink
                                                  className="nav-link"
                                                  onClick={this.toggleNav}
                                                  href="/#Contact-Me"
                                             >
                                                  Contact
                                             </NavLink>
                                        </NavItem>

                                        <NavItem>
                                             <NavLink
                                                  className="nav-link Resume-b"
                                                  onClick={this.toggleNav}
                                             >
                                                  Resume
                                             </NavLink>
                                        </NavItem>
                                   </Nav>
                              </Collapse>
                         </div>
                    </Navbar>

                    <div className="container">
                         <div className="row intro">
                              <Fade up>
                                   <div className="col-12 col-sm-12 col-md-7">
                                        <h3>
                                             {" "}
                                             <span>
                                                  {" "}
                                                  <img
                                                       src={Hi}
                                                       className="animated-hello"
                                                       alt="hi"
                                                  />
                                             </span>{" "}
                                             <span className="my-name">
                                                  Hi, My Name is,
                                             </span>
                                        </h3>
                                        <h1> Md Yaseen Ahmed.</h1>
                                        <div className="auto-typing">
                                             <Typical
                                                  steps={[
                                                       "Student",
                                                       1200,
                                                       "FreeLancer",
                                                       1200,
                                                       "Web Developer",
                                                       1200,
                                                       "Tech Enthusiast",
                                                       1200,
                                                  ]}
                                                  loop={Infinity}
                                             />
                                        </div>

                                        <a href="mailto:mdyaseenahmed1@gmail.com">
                                             <span className="Resume-btn-02">
                                                  Get in Touch
                                             </span>
                                        </a>
                                   </div>
                              </Fade>
                              <Fade right up>
                                   <div className="col-12 col-sm-12 col-md-5">
                                        <img
                                             src={Banner}
                                             width="100%"
                                             alt="Avatar"
                                             className="banner-img"
                                        />
                                   </div>
                              </Fade>
                         </div>
                    </div>
               </React.Fragment>
          );
     }
}

export default Header;
