import React from "react";
import Particles from "react-particles-js";

class ParticlesComponent extends React.Component {
     render() {
          return (
               <Particles
                    canvasClassName="part"
                    // height="100%"
                    width="100%"
                    params={{
                         particles: {
                              number: {
                                   value: 300,
                                   density: {
                                        enable: false,
                                   },
                              },
                              size: {
                                   value: 3,
                                   random: true,
                                   anim: {
                                        speed: 4,
                                        size_min: 0.3,
                                   },
                              },
                              line_linked: {
                                   enable: false,
                              },
                              move: {
                                   random: true,
                                   speed: 1,
                                   direction: "top",
                                   out_mode: "out",
                              },
                         },
                    }}
               />
          );
     }
}

export default ParticlesComponent;
